function JamShare() {
  return (
    <div>
      <h2>Jam Share - Like Instagram, But For Music</h2>
      <p class="bottom-paragraph">
        As a young adult, I am guilty of spending hours of my life mindlessly
        scrolling on Instagram or Spotify. Spotify especially has me locked in
        with its endless supply of music recommendations. While I liked
        Spotify's curated playlists, I wished there was an easy way to checkout
        song recommendations from my friends. Starting with this idea, I made
        Jam Share, a full stack application which allows users to share and post
        about their song recommendations! I took inspiration from Instagram
        where users can post, comment, and like other posts. In the process of
        working on this project, I increased my knowledge of full stack
        development and how an application goes from idea to reality. Click{" "}
        <a href="https://www.jam-share.com/">here</a> to view the project (due
        to deep integration with Spotify's API and limited number of users
        available, please sign in using username: JamShareTest@gmail.com, password: JamShareTest). Click{" "}
        <a href="https://github.com/eninodel/JamShare">here</a> to view the
        repository.
      </p>
      <hr />
    </div>
  );
}

export default JamShare;
